<template>
    <v-dialog
        v-model="dialog"
        max-width="1000"
        id="class-form"
        persistent
    >
        <v-card >
             <v-toolbar color="#CB3737" dark flat>
                <v-spacer></v-spacer>  
                <v-toolbar-title>
                    {{ `${action == 'view' ? 'View' : action == 'edit' ? 'Edit' : 'Add New'}` }} Class
                </v-toolbar-title>
                <v-spacer></v-spacer>
                 <v-tooltip  bottom>
                <template v-slot:activator="{ on }">
                    <v-icon align="left" v-on="on" @click="dialog = false">mdi-close</v-icon>
                </template>
                <span>Close</span>
                </v-tooltip>
            </v-toolbar>
            <v-card-text class="px-10 py-7">
                <v-form ref="formData" >
                    <v-container class="px-10">
                        <div class="my-4 title black--text">Grade and Section </div>
                        <v-row>
                            <v-col
                                cols="12"
                                md="6"
                            >
                            <span class="my-4 text-subtitle-1"> Grade Level</span>
                              <v-select
                                placeholder="Select grade level"
                                hide-details
                                outlined
                                dense
                                >
                                </v-select>
                            </v-col>
                             <v-col
                                cols="12"
                                md="6"
                            >
                            <span class="my-4 text-subtitle-1"> Section </span>
                              <v-select
                                placeholder="Select section"
                                hide-details
                                outlined
                                dense
                                >
                                </v-select>
                            </v-col>
                        </v-row>
                        <div class="my-4 title black--text">Subject and Professor</div>
                        <v-row>
                            <v-col
                                cols="12"
                                md="6"
                            >
                            <span class="my-4 text-subtitle-1">Subject</span>
                              <v-select
                                placeholder="Select subject"
                                hide-details
                                outlined
                                dense
                                >
                                </v-select>
                            </v-col>
                             <v-col
                                cols="12"
                                md="6"
                            >
                            <span class="my-4 text-subtitle-1"> Professor </span>
                              <v-select
                                placeholder="Select professor"
                                hide-details
                                outlined
                                dense
                                >
                                </v-select>
                            </v-col>
                        </v-row>
                        <div class="my-4 title black--text">Schedule</div>
                        <v-row>
                            <v-col
                                cols="12"
                                md="6"
                            >
                            <span class="my-4 text-subtitle-1">Date and Time</span>
                              <v-select
                                placeholder="Select date and time"
                                hide-details
                                outlined
                                dense
                                >
                                </v-select>
                            </v-col>
                             <v-col
                                cols="12"
                                md="6"
                            >
                            </v-col>
                        </v-row>
                        
                   
                    
                   
                    
                    </v-container>
                </v-form>
            
            </v-card-text>
            <v-card-actions class="px-10">
            <v-container class="px-10">
                 <v-row class="px-2 pb-5">
                <v-spacer></v-spacer>
                <v-btn v-show="action !== 'view'"
                    class="pa-5"
                    color="#A4A6B3"
                    width="150"
                    depressed
                    dark
                    text
                    @click="dialog = false"
                >
                    <span class="text-capitalize">Cancel</span>
                </v-btn>
                <v-btn
                    class="pa-5 ml-3"
                    color="#CB3737"
                    width="150"
                    depressed
                    dark
                    text
                >
                    <span class="text-capitalize">{{this.action ==='view' ? 'Edit' : this.action === 'add' ? 'Add' : 'Save'}}</span>
                </v-btn>
            </v-row>
            </v-container>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    name:'class-form',
    props:{
        show: { type: Boolean, default: false, required: true },
        action: { type: String, default: '' },
        data: { type: Object, default: new Object() },
        index: { default:''}
    },
    data(){
        return{
            dialog: this.show
        }
    },
    watch:{
        show(value) {
                this.dialog = value
                // if(value){ 
                //     this.initialize()
                // }
            },
        dialog(value){
                 if (!value) {
                    setTimeout(()=>{
                    // this.clearForm();
                    this.$emit('close');
                     }, 1000)
                }
            },
    }
}
</script>

<style lang="sass" scoped>

</style>