import {UserService} from '../../services'

const state = {
    user: [],
}

const getters = {}

const mutations = {
    SET_USER_DATA: (state,payload) => state.user = payload,
}

const actions = {

    GET_USER_PROFILE: async ({commit}) => {
        try{
          const result = await UserService.getUserProfile()
          commit("SET_USER_DATA", result.data)
        }catch(error){
          console.log(error)
        }
      },
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}