<template>
    <v-card id="main-page" class="pa-5" height="100%">
        <v-card-title class="title">
            <span>Class Info</span>
        </v-card-title>
        <v-card-text>
            <v-row>
                <v-col cols="12" md="7">
                    <v-row>
                        <v-col cols="12" md="4">
                            <v-text-field
                            placeholder="Search Class"
                            prepend-inner-icon="mdi-magnify"
                            hide-details
                            outlined
                            dense
                            >
                            </v-text-field>
                        </v-col>
                        <v-col cols="12" md="4">
                            <v-select
                            :items="gradeLevel"
                            label="Grade"
                            hide-details
                            outlined
                            dense
                            >
                            </v-select>
                        </v-col>
                        <v-col cols="12" md="4">
                            <v-select
                            :items="sections"
                            label="Section"
                            hide-details
                            outlined
                            dense
                            >
                            </v-select>
                        </v-col>
                    </v-row>
                </v-col>
                <v-spacer></v-spacer>
                <v-col cols="12" md="2" class="text-right">
                     <v-btn 
                        class="text-capitalize"
                        color="#CB3737" 
                        outlined
                        elevation="0" 
                        height="39"
                         @click="action({ action: 'add' })"
                        >
                        <v-icon left> mdi-clipboard-list-outline </v-icon>
                        <span>View Teachers</span>
                    </v-btn>
                </v-col>
                <v-col cols="12" md="2" class="text-right">
                     <v-btn 
                        class="text-capitalize"
                        color="#CB3737" 
                        outlined
                        elevation="0" 
                        height="39"
                         @click="action({ action: 'add' })"
                        >
                        <v-icon left> mdi-clipboard-list-outline </v-icon>
                        <span>View Students</span>
                    </v-btn>
                </v-col>
            </v-row>
        </v-card-text>
        <v-card-text>
            <v-data-table
                :headers="dataTable.headers"
                 hide-default-footer
                >
             </v-data-table>
        </v-card-text>
        <class-form 
          v-bind="classForm"  
          @close="classForm = { ...classForm, show: false, data: {}}"
          @edit="action({ ...classForm, action: 'edit', show: true })"
        >
        </class-form>
    </v-card>
</template>

<script>
import SelectionMixins from '../../../mixins/selectionMixins.js'
import classForm from './forms/class-form.vue';
export default {
    name:'class',
    components:{
        classForm
    },
    mixins: [SelectionMixins],
    data(){
        return{
            dataTable:{
                headers:[
                    {text:'Teacher',width:'5%'},
                    {text:'Subject Code',width:'5%'},
                    {text:'Grade',width:'5%',sortable: false},
                    {text:'Section',width:'5%',sortable: false},
                    
                ]
            },
            classForm: { show: false, action: '', data: {} },
        }
    },
    methods:{
        action({action, data, index, message}){
            if(action === 'add'){
                this.classForm = {show: true, action, data:{}}
            }
        }
    }
}
</script>

<style lang="scss" scoped>
#main-page{
    .add-button {
    height: 39px;
    background-color: #04c35c;
    text-transform: none;
    // width: 168px;
    // font-size: 12px;
    // font-weight: 400;
    color: #fff;
    .v-icon {
      color: #fff;
    }
  }
}
</style>