<template>
    <v-card id="main-page" class="pa-5" height="100%">
        <v-card-title class="title">
            <span>Subjects Management</span>
        </v-card-title>
        <v-card-text>
            <v-row>
                <v-col cols="12" md="7">
                    <v-row>
                        <v-col cols="12" md="4">
                            <v-text-field
                            placeholder="Search Class"
                            prepend-inner-icon="mdi-magnify"
                            hide-details
                            outlined
                            dense
                            >
                            </v-text-field>
                        </v-col>
                        <v-col cols="12" md="4">
                            <v-select
                            v-model="gradeLevel"
                            :items="gradeLevels"
                            label="Grade"
                            hide-details
                            outlined
                            dense
                            >
                            </v-select>
                        </v-col>
                        <!-- <v-col cols="12" md="4">
                            <v-select
                            :items="sections"
                            label="Section"
                            hide-details
                            outlined
                            dense
                            >
                            </v-select>
                        </v-col> -->
                    </v-row>
                </v-col>
                <v-spacer></v-spacer>
                <v-col cols="12" md="2" class="text-right">
                     <v-btn 
                        class="text-capitalize" 
                        color="success"
                        elevation="0" 
                        outlined
                        height="39"
                         @click="action({ action: 'add' })"
                        >
                        <v-icon left> mdi-plus-box-outline </v-icon>
                        <span>Create Subject</span>
                    </v-btn>
                </v-col>
            </v-row>
        </v-card-text>
        <v-card-text>
            <v-data-table
                :headers="dataTable.headers"
                :items="subjects"
                 hide-default-footer
                >
             </v-data-table>
        </v-card-text>
        <class-form 
          v-bind="classForm"  
          @close="classForm = { ...classForm, show: false, data: {}}"
          @edit="action({ ...classForm, action: 'edit', show: true })"
        >
        </class-form>
    </v-card>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import SelectionMixins from '../../../mixins/selectionMixins.js'
import classForm from './forms/class-form.vue';
export default {
    name:'class',
    components:{
        classForm
    },
    mixins: [SelectionMixins],
    data(){
        return{
            dataTable:{
                headers:[
                    {text:'Subject Code',value:'code',width:'5%'},
                    {text:'Subject Title',value:'title',width:'5%'},
                    {text:'Units',width:'5%',value:'units',sortable: false},
                    {text:'Actions',width:'2%',sortable: false},
                    
                ]
            },
            classForm: { show: false, action: '', data: {} },
            gradeLevel:'All',
            gradeLevels: [
                {text:'All',value:'All'},
                {text:'Grade 7',value:'7'},
                {text:'Grade 8',value:'8'},
                {text:'Grade 9',value:'9'},
                {text:'Grade 10',value:'10'},
                {text:'Grade 11',value:'11'},
                {text:'Grade 12',value:'12'},
                ]
        }
    },
    computed:{
        ...mapState("subject",["subjects"]),
    },
    watch:{
        gradeLevel(v){
            console.log(v)
            this.GET_SUBJECTS_DATA()
        }
    },
    mounted(){
        this.GET_SUBJECTS_DATA()
    },
    methods:{
        ...mapActions("subject",["GET_SUBJECTS"]),
        action({action, data, index, message}){
            if(action === 'add'){
                this.classForm = {show: true, action, data:{}}
            }
        },
        GET_SUBJECTS_DATA(){
            let payload = {};
            if(this.gradeLevel != 'all'){
                payload = {
                    'grade_level': this.gradeLevel
                }
            }
            this.GET_SUBJECTS(payload)
        }
    }
}
</script>

<style lang="scss" scoped>
#main-page{
    .add-button {
    height: 39px;
    background-color: #04c35c;
    text-transform: none;
    // width: 168px;
    // font-size: 12px;
    // font-weight: 400;
    color: #fff;
    .v-icon {
      color: #fff;
    }
  }
}
</style>