<template>
   
    <div id="app-bar">
    <v-app-bar
        app
        color="#B71C1C"
        outlined
        height="80"
    >
      <!-- <v-app-bar-nav-icon dark @click.stop="drawer = !drawer"></v-app-bar-nav-icon> -->

      <v-toolbar-title class="white--text">
          {{ title }}
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-form ref="userForm">
          <v-container class="mt-5" height="80">
              <v-row cols="12">
                  <v-col md="5">
                       <v-text-field
                        v-model="formData.email"
                        label="Enter email"
                        solo
                        flat
                        outlined
                        dense
                        :rules="rules.emailRules"
                        ></v-text-field>
                  </v-col>
                  <v-col md="5">
                      <v-text-field
                        v-model="formData.password"
                        label="Enter password"
                        solo
                        flat
                        outlined
                        dense
                        :rules="rules.passwordRules"
                        type="password"
                        ></v-text-field>
                  </v-col>
                  <v-col md="2">
                      <v-btn
                        color="success"
                        depressed
                        height="39"
                        @click="handleSubmit"
                      >
                      <span class="text-capitalize">Sign In</span>
                      </v-btn>
                  </v-col>
              </v-row>
          </v-container>
      </v-form>
        
    </v-app-bar>
    <v-card >
            <!-- <v-card-title height="300" class="grey lighten-4 mx-auto" >
            
            </v-card-title> -->
            <v-row>
                <v-col cols="12" md="6">
                    <v-img
                    :src="'./assets/homepage.png'"
                    height="100%"
                    ></v-img>
                </v-col>
            </v-row>
            
        </v-card>
    </div>
    
</template>

<script>
export default {
    name:'login',
    data(){
            return{
                drawer: true,
                links: [
                    {
                        icon: "mdi-home-city-outline",
                        text: "Dashboard",
                        route: "/dashboard",
                    },
                    {
                        icon: "mdi-home-city-outline",
                        text: "Dashboard",
                        route: "/dashboard",
                    },
                    {
                        icon: "mdi-home-city-outline",
                        text: "Dashboard",
                        route: "/dashboard",
                    },
                 ],
                 formData:{
                     email:'',
                     password:''
                 },
                 rules:{
                   emailRules: [
                        value=> !! value || "Email Address is required",
                        value => this.emailRegex.test(value) || 'E-mail must be valid',
                    ],
                    passwordRules:[
                        value =>  !! value || "Password is required",
                        // value => this.passwordRegex.test(value) || "Password is invalid.",
                    ],
                 },
                 emailRegex: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                 passwordRegex: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[A-Za-z\d@$!%*?&]{8,}$/,
            }
        },
        computed:{
            title(){
                return `${process.env.MIX_SYSTEM_NAME}`
            }

        },
        methods:{
          handleSubmit(){
            let formStatus = this.$refs.userForm.validate()

            if(formStatus){

                // let {email, password} = this.formData
                this.$store.dispatch("auth/login", this.formData)  
            }

            console.log(formStatus)
          }
        }
}
</script>

<style lang="scss" scoped>

</style>